import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { createStore, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import { Provider } from 'react-redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import { bsUiModelReducer } from './model';
import { BsUiModelState } from './type';
import { Template } from './component';
import './asset/bootstrap.css';
import 'normalize.css/normalize.css';
import 'flexboxgrid/dist/flexboxgrid.min.css';
import 'font-awesome/css/font-awesome.min.css';

const store = createStore<BsUiModelState>(bsUiModelReducer, composeWithDevTools(applyMiddleware(thunk)));

ReactDOM.render(
  <Provider store={store}>
    <Template />
  </Provider>,
  document.getElementById('root') as HTMLElement
);

import AssetPool from '@brightsign/assetpool';
console.log(AssetPool);

import AssetPoolFetcher from '@brightsign/assetpoolfetcher';
console.log(AssetPoolFetcher);

import AssetRealizer from '@brightsign/assetrealizer';
console.log(AssetRealizer);

import Compistor from '@brightsign/compositor';
console.log(Compistor);

import DecoderConfiguration from '@brightsign/decoderconfiguration';
console.log(DecoderConfiguration);

import DwsConfiguration from '@brightsign/dwsconfiguration';
console.log(DwsConfiguration);

import HostConfiguration from '@brightsign/hostconfiguration';
console.log(HostConfiguration);

import Keyboard from '@brightsign/keyboard';
console.log(Keyboard);

import Keystore from '@brightsign/keystore';
console.log(Keystore);

import NetworkConfiguration from '@brightsign/networkconfiguration';
console.log(NetworkConfiguration);

import NetworkDiagnostics from '@brightsign/networkdiagnostics';
console.log(NetworkDiagnostics);

import Pointer from '@brightsign/pointer';
console.log(Pointer);

import PointerCalibration from '@brightsign/pointercalibration';
console.log(PointerCalibration);

import Registry from '@brightsign/registry';
console.log(Registry);

import Screenshot from '@brightsign/screenshot';
console.log(Screenshot);

import StorageInfo from '@brightsign/storageinfo';
console.log(StorageInfo);

import FileSystemInfo from '@brightsign/filesysteminfo';
console.log(FileSystemInfo);

import System from '@brightsign/system';
console.log(System);

import VideoInput from '@brightsign/videoinput';
console.log(VideoInput);

import VideoModeConfiguration from '@brightsign/videomodeconfiguration';
console.log(VideoModeConfiguration);

import VideoOutput from '@brightsign/videooutput';
console.log(VideoOutput);

const vmc = new VideoModeConfiguration();
vmc.getBestMode('hdmi')
  .then((mode) => {
    const configData: any = {};
    configData.modeName = mode;
    return vmc.setMode(configData);
  })
  .then((data) => {
    if (data.restartRequired === true) {
      const system = new System();
      system.reboot();
    } else {
      console.log('Reboot not required.')
    }
  });



